@import url('https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC');

.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

header h1{
  font-family:"Playfair Display SC";
  text-align: center;
  font-size: 64px;
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  padding: 10px;  
  width: 10%;
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;  
  width:10%;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}

tbody td{
  text-align: center;
  width:10%
}

img {
  object-fit: scale-down;
  width: 100%;
  height: 100%;
}

/* Column Size for thead */
tbody tr:nth-child(odd){
  background-color: #fff0c7;
}

td:nth-child(4){
  width:30%
}

td:nth-child(5){
  width:30%;
}

thead {
vertical-align: middle;
}

thead tr th:nth-child(4){
  width:30%
}

thead tr th:nth-child(5){
  width:30%
}



/* Column size for tbody */
tbody {
  display: table-row-group;
  vertical-align: middle;
}
